ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

a {
  display: block;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

input {
  outline: none;
}

fieldset {
  padding: 0;
  border: none;
  margin: 0;
}

:root {
  --color-shadow-light: rgb(256 256 256 / 40%);
  --color-shadow-dark: rgb(0 0 0 / 40%);
}

:root[theme=dark] {
  --color-primary: #360e40;
  --color-bg-button: #2e2448;
  --color-button-active: #df99fc;
  --color-background-backdrop: rgb(64 63 61 / 80%);
  --color-cell-initial: #360e40;
  --color-cell-guessed: #fff;
  --color-bg-clue: #d26efb;
  --color-main-border: #e9cbe3;
}

:root[theme=light] {
  --color-primary: #baa2b5;
  --color-bg-button: #283d14;
  --color-button-active: #f2f3ec;
  --color-background-backdrop: rgb(64 63 61 / 80%);
  --color-cell-initial: #fff;
  --color-cell-guessed: #000;
  --color-bg-clue: #baa2b5;
  --color-main-border: #745d4d;
}

@keyframes burger-close {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes burger-open {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
.btn {
  transition: all 0.5s ease-out;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: Overlock, sans-serif;
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}
:root[theme=dark] body {
  background: center no-repeat url("../images/bg_dark.webp");
  background-size: cover;
}
:root[theme=light] body {
  background: center no-repeat url("../images/bg_light.webp");
  background-size: cover;
}

h1 {
  font-size: 5rem;
  font-weight: 900;
  letter-spacing: normal;
  line-height: 1.05;
  text-transform: none;
  color: var(--color-primary);
}
@media screen and (width <= 768px) {
  h1 {
    font-size: 4rem;
  }
}
@media screen and (width <= 480px) {
  h1 {
    font-size: 2rem;
  }
}

.btn {
  border-radius: 5px;
  background-color: var(--color-bg-button);
  color: var(--color-bg-clue);
  font-size: 2rem;
  font-weight: 900;
  letter-spacing: normal;
  line-height: 1.05;
  text-transform: none;
}
.btn:has(img) {
  font-size: 0;
}
.btn:active {
  filter: drop-shadow(2px 3px 3px var(--color-shadow-dark));
  opacity: 0.7;
}

.modal {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: var(--color-background-backdrop);
  visibility: hidden;
}
.modal_active {
  visibility: visible;
}

.modal__content {
  display: flex;
  width: 100%;
  max-width: 600px;
  flex-direction: column;
  align-items: center;
  padding: 40px 60px;
  border-radius: 20px;
  background-color: var(--color-main-border);
  gap: 40px;
  text-align: center;
  transform: translateY(-200%);
  transition: all 0.5s ease-in-out;
}
.modal_active .modal__content {
  transform: translateY(0%);
}
:root[theme=light] .modal__content {
  background-color: #d4d8d0;
}
@media screen and (width <= 480px) {
  .modal__content {
    padding: 20px;
  }
}

.modal__title {
  font-size: 4rem;
  font-weight: 900;
  letter-spacing: normal;
  line-height: 1.05;
  text-transform: none;
  color: var(--color-primary);
}